import React from 'react'
import {
    Container,
    Row,
    Col,
    Card,
    CardImgOverlay,
    CardImg,
    CardBody,
    CardTitle,
    CardText,
} from 'reactstrap'
import NavbarContent from '../components/navigation/navbar'
import { Img } from 'react-image'
import myimg from '../images/blog-content/NUS.jpg'
import myimg2 from '../images/blog-content/hackathon1.jpg'

const AboutUs = () => {
    return (
        <>
            <Card
                style={{
                    alignItems: 'center',
                    maxHeight: '90vh',
                }}
            >
                <CardImgOverlay
                    style={{
                        justifyContent: 'center',
                        alignContent: 'center',
                        maxWidth: '80vw',
                    }}
                >
                    <CardTitle style={{ color: 'maroon' }} tag="h2">
                        Who are We - Our Founder's Story
                    </CardTitle>
                    <CardText style={{ fontSize: '30px', color: 'black' }}>
                        <p className="overlaywhite">
                            Our Founders Youssef El Mokhtari and Robert Daniel
                            Cook realised that with the fast progression of
                            technology and the slow adoption of the new
                            technologies being taught in the education system
                            they realised a lot of students in JCU are
                            unprepared for the industry requirements and thats
                            where we come in. We provide guidance, training and
                            experience of on how development
                        </p>
                    </CardText>
                </CardImgOverlay>
            </Card>
        </>
    )
}

